

// We use these to build padding for buttons.
$button-med: rem-calc(12);
$button-tny: rem-calc(7);
$button-sml: rem-calc(9);
$button-lrg: rem-calc(16);

// We use this to control the display property.
$button-display: inline-block;
$button-margin-bottom: rem-calc(20);

// We use these to control button text styles.
$button-font-family: inherit;
$button-font-color: #fff;
$button-font-color-alt: #333;
$button-font-med: rem-calc(16);
$button-font-tny: rem-calc(11);
$button-font-sml: rem-calc(13);
$button-font-lrg: rem-calc(20);
$button-font-weight: bold;
$button-font-align: center;

// We use these to control various hover effects.
$button-function-factor: 10%;

// We use these to control button border styles.
$button-border-width: 1px;
$button-border-style: solid;

// We use this to set the default radius used throughout the core.
$button-radius: 4px;
$button-round: 4px;

// We use this to set default opacity and cursor for disabled buttons.
$button-disabled-opacity: 0.6;
$button-disabled-cursor: pointer;

// We use these to style medium dropdown buttons
$dropdown-button-padding-med: 3rem;
$dropdown-button-pip-size-med: 3rem;
$dropdown-button-pip-opposite-med: 3rem;
$dropdown-button-pip-top-med: 3rem;

input:focus,
select:focus,
textarea:focus,
button:focus {
    outline: none;
}



button::-moz-focus-inner {
  border: 0;
  padding: 0; }

